.App {
    height: 100vh;
    overflow-y: scroll;
    overflow-x: hidden;

    background-color: #ffffff;
    opacity: 1;
    background-image: linear-gradient(#ddd 1px, transparent 1px), linear-gradient(to right, #ddd 1px, #ffffff 1px);
    background-size: 30px 30px;

    .wrap {
        max-width: 740px;
        margin: 0 auto;
    }

    header {
        position: fixed;
        top: 0px;

        // width: 100%;
        // height: 100px;
        img {
            margin: 8px;
        }
    }

    .Title {
        img {
            max-width: 100%;
        }
        height: 100vh;
        display: grid;
        align-content: center;
        justify-content: center;
        padding: 16px;

        text-align: center;
    }

    .PathPage {
        // margin-top: 60px;
        width: 100%;
        height: 100vh;

        text-align: center;
        display: flex;
        flex-direction: column;
        justify-content: center;

        .searchWrap {
            display: flex;
            flex-direction: column;
            align-items: center;
            margin-top: 50px;

            input {
                width: 180px;
                text-align: center;
                font-size: 16px;
                margin-bottom: 8px;
            }

            button {
                width: fit-content;
            }
        }
    }
}
