.Poster {
    text-align: center;

    img {
        max-width: 100%;
        max-height: 85vh;
        box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    }

    .poster {
        position: sticky;
        top: 0;

        height: 100vh;
        display: grid;
        align-content: center;
        justify-content: center;
    }
    .poster:nth-child(2) {
        height: 110vh;
    }
    .poster:nth-child(3) {
        height: 120vh;
    }
    .poster:last-child {
        height: 130vh;
    }
}

.Poster::-webkit-scrollbar {
    display: none;
}
